import React from 'react'
import Head from 'next/head'
import Router from 'next/router'
import Link from 'next/link'
import Case from 'case'
import { bool, shape, number, func, string } from 'prop-types'
import _ from 'lodash'
import { Layout, Menu, Breadcrumb, Icon, BackTop } from 'antd';
// import { logo } from '../../assets/images'
import { menuBar } from '../../config/menuBar'
import stylesheet from 'antd/dist/antd.min.css'
import css from './styles.css'
// import { AuthFacebook } from '../../features'

const { Content, Footer, Sider } = Layout

class Layouts extends React.Component {
  static propTypes = {
    title: string,
    isMobile: bool.isRequired,
    dimenstion: shape({
      width: number.isRequired,
      height: number.isRequired,
    }).isRequired,
    url: shape({
      pathname: string.isRequired
    }).isRequired,
  }

  static defaultProps = {
    title: 'Title'
  }

  constructor(props) {
    super(props)

    this.state = {
      collapsed: false,
      positionFixed: true,
    }

    this.onCollapse = this.onCollapse.bind(this)
    this.onMenuKey = this.onMenuKey.bind(this)
  }

  onCollapse(collapsed) {
    this.setState({ collapsed });
  }

  onMenuKey({ key, keyPath }) {
    const { url } = this.props
    if(key !== url.pathname) {
      Router.push(key)
    }
  }

  getSize() {
    const { isMobile, dimenstion } = this.props
    if(isMobile) {
      return {
        minWidth: dimenstion.width
      }
    }
    return {}
  }
  
  render() {
    const { url, isMobile, children, title } = this.props
    const { collapsed, positionFixed } = this.state
    const breadcrumbs = url.pathname.split(new RegExp('/','g')).slice(1)
    const keyPath = _.get(url, 'state.keyPath', [url.pathname])
    const menuStyle = positionFixed ? {
      position: 'fixed',
      height: `100%`,
      zIndex: 500
    } : {}
    const layoutStyle = positionFixed ? { marginLeft: !collapsed ? 200 : 0 } : {}
    return (
      <Layout style={{ minHeight: '100vh' }}>
        <Head>
          <title>{title}</title>
          <meta charSet='utf-8' />
          <meta name='viewport' content='initial-scale=1.0, width=device-width' />
          <style dangerouslySetInnerHTML={{ __html: stylesheet + css }} />
        </Head>
        <Sider
          breakpoint="md"
          collapsedWidth="0"
          onCollapse={this.onCollapse}
          style={menuStyle}
        >
          <div className='ant-logo'>
            <img alt='logo' src={''} className='ant-logo-img' />
          </div>
          <Menu theme="dark" defaultSelectedKeys={keyPath} mode="inline" onClick={this.onMenuKey} >
            {
              menuBar.map(item => (
                <Menu.Item key={item.path} >
                  <Icon type={item.icon} />
                  <span>{item.title}</span>
                </Menu.Item>
              ))
            }
          </Menu>
        </Sider>
        <Layout style={layoutStyle}>
          { !collapsed && isMobile && <div className='dark-bg' /> }
          <Content>
            <Breadcrumb style={{ margin: '16px 24px' , ...this.getSize()}}>
              <Breadcrumb.Item><Icon type="home" /> <Link href='/'>Home</Link></Breadcrumb.Item>
              {
                breadcrumbs.map((item, i) => (
                  (i !== breadcrumbs.length - 1) ? (
                    <Breadcrumb.Item key={item}><Link href={`/${item}`}>{Case.capital(item)}</Link></Breadcrumb.Item>
                  ) : (
                    <Breadcrumb.Item key={item}>{Case.capital(item)}</Breadcrumb.Item>
                  )
                ))
              }
            </Breadcrumb>
            <div className='pull-right'>
              {/* <AuthFacebook isMobile /> */}
            </div>
            <div style={{ padding: 24, background: '#fff', minHeight: 300, ...this.getSize() }}>
              {children}
            </div>
            <BackTop />
          </Content>
          <Footer style={{ textAlign: 'center', ...this.getSize() }}>
            Ant Design 2016 Created by Ant UED
          </Footer>
        </Layout>
      </Layout>
    );
  }
}

export default Layouts



